import React from 'react';
import s from './table.module.css';
import TableCell from './TableCell';

const TableRow = ({data}) => {
  return (
    <tr className={s.row}>
      <td className={s.cell}>
        <img className={s.cell__logo} src={data.coin_img} alt={data.coin_name}/>
        {data.coin_name} ({data.coin})
      </td>
      <TableCell value={data.price}/>
      <TableCell value={data.balance}/>
      <TableCell value={data.frozen}/>
      <TableCell value={data.available}/>
    </tr>
  );
};

export default TableRow;