import React from 'react';
import s from './summary.module.css';

const Summary = ({data}) => {
  const calcSum = name => {
    return data.reduce((acc, coin) => {
      return acc + coin.price * coin[name];
    }, 0).toFixed(5) + ' USDT';
  }

  return (
    <table className={s.wrapper} cellPadding={0} cellSpacing={0}>
      <tbody>
      <tr>
        <td className={s.cell}>Общий баланс (сумма):</td>
        <td className={s.cell}>{calcSum('balance')}</td>
      </tr>
      <tr>
        <td className={s.cell}>В ордерах (сумма):</td>
        <td className={s.cell}>{calcSum('frozen')}</td>
      </tr>
      <tr>
        <td className={s.cell}>Свободный объем (сумма):</td>
        <td className={s.cell}>{calcSum('available')}</td>
      </tr>
      </tbody>
    </table>
  );
};

export default Summary;