import React from 'react';
import s from './table.module.css';
import TableRow from './TableRow';

const Table = ({data}) => {
  return (
    <div style={{overflow: 'auto', width: '100%'}}>
      <table className={s.wrapper} cellSpacing={0} cellPadding={0}>
        <thead className={s.head}>
        <tr className={s.row}>
          <th className={s.cell}>Монета</th>
          <th className={s.cell}>Курс к USDT</th>
          <th className={s.cell}>Общий баланс</th>
          <th className={s.cell}>В ордерах</th>
          <th className={s.cell}>Свободный объем</th>
        </tr>
        </thead>
        <tbody className={s.body}>
        {data.map((row, index) => <TableRow key={index} data={row}/>)}
        </tbody>
      </table>
    </div>
  );
};

export default Table;