import React from 'react';
import s from './menu.module.css';
import MenuItem from './MenuItem';

const Menu = ({open, data, setData}) => {
  return (
    <div className={`${s.wrapper} ${!open ? s.hidden : ''}`}>
      <ul className={s.list}>
        {data.map(user => <MenuItem
          key={user.id}
          data={user}
          onClick={() => setData(user)}
        />)}
      </ul>
    </div>
  );
};

export default Menu;