import React, {useRef, useState} from 'react';
import s from './panel.module.css';
import arrow from '../../../assets/arrow.svg'
import Menu from '../../menu/Menu';

const TablePanel = ({user, data, setData}) => {
  const userButton = useRef();
  const [open, setOpen] = useState(false);

  const openMenu = () => {
    setOpen(true);
  }

  const closeMenu = () => {
    setOpen(false);
    userButton.current?.blur();
  }

  const handleChangeData = data => {
    setData(data);
    closeMenu();
  }

  return (
    <div className={s.wrapper}>
      <button
        ref={userButton}
        className={s.userButton}
        onFocus={openMenu}
        onBlur={closeMenu}
        onMouseEnter={openMenu}
        onMouseLeave={closeMenu}
      >
        {user.label}{' '}
        <img className={s.userButton__icon} src={arrow} alt=''/>
        <Menu
          open={open}
          data={data}
          setData={handleChangeData}
        />
      </button>
    </div>
  );
}

export default TablePanel;