import React, {useEffect, useState} from 'react';
import {getCoinsByUserId} from '../../api/getCoinsByUserId';
import Main from './Main';

const TIMEOUT = 10 * 1000;

export const users = [
  {label: 'Пользовате 1', id: 1},
  {label: 'Пользовате 2', id: 2},
];

const MainContainer = () => {
  const [data, setData] = useState([]);
  const [user, setUser] = useState(users[0]);

  const getData = id => {
    getCoinsByUserId(id).then(setData);
  }

  useEffect(() => {
    getData(user.id);

    const interval = setInterval(() => {
      getData(user.id);
    }, TIMEOUT);

    return () => clearInterval(interval);
  }, [user]);

  return <Main
    data={data}
    user={user}
    users={users}
    setUser={setUser}
  />;
};

export default MainContainer;